class Users {
    constructor() {
        this.users = [];
    }
    AddUserData(id, room) {
        var users = {
            id,

            room
        };
        this.users.push(users);
        return users;
    }

    RemoveUser(id) {
        var user = this.GetUser(id);
        let result;

        if (user) {
            result = this.users.filter((user) => user.id === id);
            this.users = this.users.filter((user) => user.id !== id);

        }

        return result[0];
    }

    GetUser(id) {
        var getUser = this.users.filter((userId) => {
            return userId.id === id;
        })[0];
        return getUser;
    }

    GetUsersList(room) {
        var users = this.users.filter((user) => user.room === room);

        var namesArray = users.map((user) => {
            return user.name;
        });

        return namesArray;
    }
}

module.exports = {
    Users
};